<template>
  <tas-base-crud :key="mainKey" :config="config" hide-create hide-detail>
    <template v-slot:list-action>
      <a v-if="$_sys.isAllowed(config.permission.create)" v-b-modal.modal-create-simpan-pinjam class="btn btn-primary btn-icon btn-circle ml-3">
        <i class="ri-add-line p-0"></i>
      </a>
      <b-modal id="modal-create-simpan-pinjam" hide-footer no-close-on-backdrop centered size="xl" title="Tambah Periode Penggajian">
        <div class="row">
          <div class="form-group col-4 mb-0">
            <span>Periode <span class="text-danger">*</span></span>
            <s-input v-model="firstForm.periode_month" :item="{ type:'month', validation:[], placeholder: 'Pilih Periode Penggajian', api:'', nm:true, formatter:momentFormat, ic:'w-100 mt-2' }" />
          </div>
          <div class="form-group col-4 mb-0">
            <span>Tanggal Awal Pencatatan <span class="text-danger">*</span></span>
            <s-input v-model="firstForm.start_date" :item="{ type:'date', validation:[], placeholder: 'Pilih Tanggal Awal Pencatatan', api:'', nm:true, formatter:dateFormat, ic:'w-100 mt-2' }" />
          </div>
          <div class="form-group col-4 mb-0">
            <span>Tanggal Akhir Pencatatan <span class="text-danger">*</span></span>
            <s-input v-model="firstForm.end_date" :item="{ type:'date', validation:[], placeholder: 'Pilih Tanggal Akhir Pencatatan', api:'', nm:true, formatter:dateFormat, ic:'w-100 mt-2' }" />
          </div>
        </div>
        <hr>
        <div class="mb-5">
          <div class="mb-5">
            <h5 class="mb-0">Tenaga Kerja</h5>
            <span class="text-muted font-size-sm">Sesuaikan Tenaga Kerja yang terdaftar pada periode terpilih</span>
          </div>
          <div class="d-flex">
            <input v-model.lazy="employeeList.search" type="text" name="search" class="form-control max-w-350px mr-5" placeholder="Cari Nama / NIP (Nomor Induk Pegawai) Tenaga Kerja">
            <label class="radio justify-content-start">
              <input v-model="activeEmployee" type="radio" :value="false">
              <span class="mr-1"></span> Tampilkan Semua </label>
            <label class="radio justify-content-start ml-5">
              <input v-model="activeEmployee" type="radio" :value="true">
              <span class="mr-1"></span> Hanya Tampilkan yang Aktif </label>
          </div>
        </div>
        <template v-if="employeeList.data.length">
          <b-overlay :show="isLoadingEmployee" :opacity=".90">
            <table class="table table-head-custom table-vertical-center table-head-bg">
              <thead>
                <tr>
                  <th>
                    <a @click="checkAllEmployee()" class="btn btn-sm btn-primary mr-5">Pilih semua di Halaman ini</a>
                  </th>
                  <th>Jabatan</th>
                  <th>Status Ketenagakerjaan</th>
                  <th class="text-center">MPP</th>
                  <th class="text-center">PLT</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(e,i) in employeeList.data" :key="i+'-data-employee'">
                  <td>
                    <label class="checkbox">
                      <input v-model="firstForm.child_data_employee_on_periodes" type="checkbox" :value="{ employee_id: e.id, job_position_id: e.job_position_id, status_employment_id: e.status_employment_id, is_mpp: e.is_mpp, is_plt: e.is_plt }">
                      <span class="mr-3"></span>
                      <div class=" d-flex justify-content-between align-items-center flex-fill">
                        <div class="text-line-2 max-w-200px"> {{ e.fullname }} <span class="d-block font-size-sm">NIP (Nomor Induk Pegawai): {{ e.nip }}</span>
                        </div>
                        <span v-if="!activeEmployee" :class="e.status_code === 'active' ? 'label-success' : 'label-danger'" class="label label-inline font-weight-bold mr-2">{{ e.status_code | parse('status_code') }}</span>
                      </div>
                    </label>
                  </td>
                  <td>
                    <s-input @input="changeValueEmployee(e)" v-if="datasets.job_position_id.length" v-model="e.job_position_id" :item="{ attr:[{clearable: false}], type:'select', validation:[], placeholder: 'Pilih Jabatan', api:'', nm:true, notAppendToBody:true, optionList:datasets.job_position_id , ic:'w-100 mt-2' }" />
                  </td>
                  <td>
                    <s-input @input="changeValueEmployee(e)" v-if="datasets.status_employment_id.length" v-model="e.status_employment_id" :item="{ attr:[{clearable: false}], type:'select', validation:[], placeholder: 'Pilih Status Ketenagakerjaan', api:'', nm:true, notAppendToBody:true, optionList:datasets.status_employment_id , ic:'w-100 mt-2' }" />
                  </td>
                  <td>
                    <label class="checkbox justify-content-center">
                      <input @change="changeValueEmployee(e)" v-model="e.is_mpp" type="checkbox">
                      <span></span>
                    </label>
                  </td>
                  <td>
                    <label class="checkbox justify-content-center">
                      <input @change="changeValueEmployee(e)" v-model="e.is_plt" type="checkbox">
                      <span></span>
                    </label>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="d-flex justify-content-between align-items-center">
              <b-pagination class="mb-0" size="md" prev-text="Sebelumnya" next-text="Selanjutnya" hide-goto-end-buttons v-model="employeeList.page" :total-rows="employeeList.total" :per-page="employeeList.limit" />
              <span> {{ firstForm.child_data_employee_on_periodes.length }} dari {{ employeeList.total || 0 | parse('number') }} Tenaga Kerja Terpilih </span>
            </div>
          </b-overlay>
        </template>
        <div class="text-right mt-5">
          <button :disabled="!canGenerate" @click="generateMonth()" class="btn btn-primary"> Buat {{ config.title }} </button>
        </div>
      </b-modal>
    </template>
    <template v-slot:list-table-action="data">
      <a @click="$children[0].getEvent('detail', data.rowData)" class="btn btn-info btn-sm"> Lihat Detail Simpan Pinjam </a>
    </template>
    <template v-slot:header-table>
      <div class="card-body py-0">
        <div v-if="isAlertShow" class="alert alert-custom alert-light-danger p-2 mb-0" role="alert">
          <div class="alert-icon">
            <span class="svg-icon svg-icon-danger svg-icon-xl">
              <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                  <rect x="0" y="0" width="24" height="24" />
                  <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3" />
                  <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1" />
                  <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1" />
                </g>
              </svg>
            </span>
          </div>
          <div class="alert-text">Data {{ config.title }} pada bulan ini belum dibuat!</div>
        </div>
      </div>
    </template>
  </tas-base-crud>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'crud-payroll-periodes',
  data () {
    return {
      mainKey: 1,
      isAlertShow: false,
      isCheckedAll: false,
      activeEmployee: false,
      isLoadingEmployee: false,
      datasets: {
        job_position_id: [],
        status_employment_id: []
      },
      firstForm: {
        periode_month: null,
        start_date: null,
        end_date: null,
        child_data_employee_on_periodes: []
      },
      employeeList: {
        page: 1,
        search: '',
        limit: 10,
        total: 0,
        totalPage: 0,
        data: []
      },
      dateFormat: {
        stringify: (date) => {
          return date ? moment(date).format('DD MMMM YYYY') : null
        },
        parse: (value) => {
          return value ? moment(value, 'DD MMMM YYYY').toDate() : null
        }
      },
      momentFormat: {
        stringify: (date) => {
          return date ? moment(date).format('MMMM YYYY') : null
        },
        parse: (value) => {
          return value ? moment(value, 'MMMM YYYY').toDate() : null
        }
      },
      config: {
        title: 'Periode Penggajian',
        model_api: 'payroll-periodes',
        getter: 'payroll-periodes',
        setter: 'payroll-periodes',
        pk_field: null,
        permission: {
          create: 'create-payroll-periodes',
          read: 'view-payroll-periodes',
          update: 'update-payroll-periodes',
          delete: 'delete-payroll-periodes'
        },
        slave: [
          {
            name: 'Rincian',
            permission: 'view-employee-on-periodes',
            module: 'payroll/payroll-periodes-details',
            as_param: 'periode_month',
            key_field: 'periode_month'
          }
        ],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: false, detail: false, list: false } },
          { id: 'periode_month', label: 'Periode', methods: { create: false, update: false, list: { transform: 'period' }, detail: { transform: 'period' } } },
          { id: 'start_date', label: 'Tanggal Awal Pencatatan', methods: { create: false, update: { type: 'date' }, list: { transform: 'longDate' }, detail: { transform: 'longDate' } } },
          { id: 'end_date', label: 'Tanggal Akhir Pencatatan', methods: { create: false, update: { type: 'date' }, list: { transform: 'longDate' }, detail: { transform: 'longDate' } } },
          { id: 'count_day_of_work', label: 'Jumlah Hari Kerja', methods: { create: false, update: false, list: { transform: 'number' }, detail: { transform: 'number' } } }
        ]
      }
    }
  },
  watch: {
    'employeeList.page': {
      deep: false,
      immediate: false,
      handler (e) {
        this.getEmployeeList(e)
      }
    },
    activeEmployee: {
      deep: false,
      immediate: false,
      handler (e) {
        this.$set(this.firstForm, 'child_data_employee_on_periodes', [])
        this.getEmployeeList(1)
      }
    },
    'employeeList.search': {
      deep: false,
      immediate: false,
      handler (e) {
        this.getEmployeeList(1)
      }
    }
  },
  computed: {
    canGenerate () {
      return this.firstForm.periode_month && this.firstForm.start_date && this.firstForm.end_date && this.firstForm.child_data_employee_on_periodes.length
    }
  },
  async mounted () {
    this.getThisMonth()
    const job = await this.getDatasets('job-positions', null, null)
    const status = await this.getDatasets('custom/master-status-employments', null, null)
    this.$set(this.datasets, 'job_position_id', job.map(x => { return { label: x.job_position_name, code: x.id } }))
    this.$set(this.datasets, 'status_employment_id', status.map(x => { return { label: x.name, code: x.id } }))
    this.getEmployeeList()
  },
  methods: {
    checkAllEmployee () {
      let toCopy = JSON.parse(JSON.stringify(this.employeeList.data))
      // if (this.activeEmployee) toCopy = toCopy.filter(x => x.status_code === 'active')
      // filtering value
      const arrayID = this.firstForm.child_data_employee_on_periodes.map(y => Number(y.employee_id))
      toCopy = toCopy.filter(x => !arrayID.includes(Number(x.id)))
      const data = toCopy.map(e => {
        return {
          employee_id: e.id,
          job_position_id: e.job_position_id,
          status_employment_id: e.status_employment_id,
          is_mpp: e.is_mpp,
          is_plt: e.is_plt
        }
      })
      // push brand new value
      const finalData = this.firstForm.child_data_employee_on_periodes.concat(data)
      this.$set(this.firstForm, 'child_data_employee_on_periodes', finalData)
    },
    redirect (e) {
      this.$router.push({ name: e, query: { view: 'list' } }).catch((err) => { return err })
    },
    getThisMonth () {
      const formData = {
        periode_month: moment(new Date()).set('date', 1).format('YYYY-MM-DD')
      }
      this.$_api.list(this.config.setter, formData).then(res => {
        this.isAlertShow = !res.data.length
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    getEmployeeList (page = 1) {
      this.isLoadingEmployee = true
      let filter = {
        page: page,
        search: this.employeeList.search || null,
        limit: this.employeeList.limit
      }
      if (this.activeEmployee) filter.status_code = 'active'
      this.$_api.list('employees', filter).then(res => {
        let newObj = {
          page: filter.page,
          search: filter.search,
          total: res.total,
          limit: this.employeeList.limit,
          totalPage: res.totalPage,
          data: res.data
        }
        this.employeeList = Object.assign({}, newObj)
        this.isLoadingEmployee = false
      }).catch((err) => {
        this.isLoadingEmployee = false
        this.$_alert.error(err)
      })
    },
    getDatasets (api, param, obj = null) {
      return this.$_api.dataset(api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    changeTempEmployee (event) {
      const data = this.employeeList.data.map(e => {
        return {
          employee_id: e.id,
          job_position_id: e.job_position_id,
          status_employment_id: e.status_employment_id,
          is_mpp: e.is_mpp,
          is_plt: e.is_plt
        }
      })
      let target = this.firstForm.child_data_employee_on_periodes
      if (event) {
        this.$set(this.firstForm, 'child_data_employee_on_periodes', [...target, ...data])
      } else {
        this.$set(this.firstForm, 'child_data_employee_on_periodes', target.filter(x => !data.map(y => y.employee_id).includes(x.employee_id)))
      }
    },
    changeValueEmployee (data) {
      let dataID = this.firstForm.child_data_employee_on_periodes.findIndex(x => x.employee_id === data.id)
      if (dataID > -1) {
        const toPush = {
          employee_id: data.id,
          job_position_id: data.job_position_id,
          status_employment_id: data.status_employment_id,
          is_mpp: data.is_mpp,
          is_plt: data.is_plt
        }
        this.$set(this.firstForm.child_data_employee_on_periodes, dataID, toPush)
      }
    },
    generateMonth () {
      let values = this.firstForm
      values.periode_month = moment(moment(this.firstForm.periode_month, 'MMMM YYYY').toDate()).format('YYYY-MM-DD')
      values.start_date = moment(moment(this.firstForm.start_date, 'DD MMMM YYYY').toDate()).format('YYYY-MM-DD')
      values.end_date = moment(moment(this.firstForm.end_date, 'DD MMMM YYYY').toDate()).format('YYYY-MM-DD')
      this.$_api.post('custom/payroll-periodes/create-with-employee-on-periodes', values).then(res => {
        this.$children[0].getEvent('detail', res.data)
        this.isAlertShow = false
        this.$set(this.firstForm, 'periode_month', null)
        this.$_alert.success(null, this.config.title + ' berhasil dibuat')
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



